import React from "react";
import ClassNames from "classnames";
import { DragSource, DropTarget } from "react-dnd";
import { NativeTypes } from "react-dnd-html5-backend";
import { formatDistanceToNow } from "date-fns";

import BaseFile, { BaseFileConnectors } from "./../base-file.js";
import { fileSize } from "./utils.js";

class RawTableFile extends BaseFile {
  render() {
    const {
      isDragging,
      isDeleting,
      isRenaming,
      isOver,
      isSelected,
      action,
      url,
      browserProps,
      connectDragPreview,
      depth,
      isShouldShowShared,
      sharer,
      size,
      status,
      modified,
    } = this.props;

    const icon =
      browserProps.icons[this.getFileType()] || browserProps.icons.File;
    const inAction = isDragging || action;

    const ConfirmDeletionRenderer = browserProps.confirmDeletionRenderer;

    let name;
    if (!inAction && isDeleting && browserProps.selection.length === 1) {
      name = (
        <ConfirmDeletionRenderer
          handleDeleteSubmit={this.handleDeleteSubmit}
          handleFileClick={this.handleFileClick}
          url={url}
        >
          {icon}
          {this.getName()}
        </ConfirmDeletionRenderer>
      );
    } else if (!inAction && isRenaming) {
      name = (
        <form className="renaming" onSubmit={this.handleRenameSubmit}>
          {icon}
          <input
            ref={(el) => {
              this.newNameRef = el;
            }}
            type="text"
            value={this.state.newName}
            onChange={this.handleNewNameChange}
            onBlur={this.handleCancelEdit}
            autoFocus
          />
        </form>
      );
    } else {
      name = (
        <a href={url || "#"} download="download" onClick={this.handleFileClick}>
          {icon}
          {this.getName()}
        </a>
      );
    }

    let draggable = (
      <div>
        <span className="encrypted-icon">
          {this.props.status === "encrypted" ? browserProps.icons.Lock : null}
        </span>
        {name}
      </div>
    );
    if (typeof browserProps.moveFile === "function") {
      draggable = connectDragPreview(draggable);
    }

    const row = (
      <tr
        className={ClassNames("file", {
          pending: action,
          dragging: isDragging,
          dragover: isOver,
          selected: isSelected,
        })}
        onClick={this.handleItemClick}
        onDoubleClick={this.handleItemDoubleClick}
      >
        <td className="name">
          <div style={{ paddingLeft: depth * 16 + "px" }}>{draggable}</div>
        </td>
        {this.props.isShouldShowShared ? (
          <td className="size">{sharer}</td>
        ) : null}
        <td className="size">{fileSize(size)}</td>
        {/* <td className="modified">{status}</td> */}
        <td className="modified">
          {typeof modified === "undefined"
            ? "-"
            : formatDistanceToNow(modified, { addSuffix: true })}
        </td>
      </tr>
    );

    return this.connectDND(row);
  }
}

@DragSource(
  "file",
  BaseFileConnectors.dragSource,
  BaseFileConnectors.dragCollect
)
@DropTarget(
  ["file", "folder", NativeTypes.FILE],
  BaseFileConnectors.targetSource,
  BaseFileConnectors.targetCollect
)
class TableFile extends RawTableFile {}

export default TableFile;
export { RawTableFile };
